.container {
  align-items: center;
  justify-content: center;
  border-radius: var(--common-border-radius-s);
  max-width: 536px;
  width: 100%;
  min-height: 80px;
  font-family: 'Jet Brains Mono';
  font-size: 16px;
  line-height: 24px;
  color: #f2f2f3;
  flex-grow: 1;
  box-sizing: border-box;
  cursor: grab;
}
.element {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}
.list {
  list-style: none;
  display: flex;
  align-items: center;
}
.list svg {
  margin-right: 8px;
}
.listElement {
  max-width: 536px;
  width: 100%;
}
